<template>
  <div id="new_film">
    <div id="search_results">
      <img v-for="el of movies" :key="el.id" :src="el" alt />
    </div>
    <div id="slider_news">
      <h3>A la Une</h3>
      <carousel :perPage="1" :autoplay="true" :paginationEnabled="false">
        <slide v-for="item of affiche_movie" :key="item">
          <img :src="item" alt />
        </slide>
      </carousel>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Carousel, Slide } from "vue-carousel";
import store from "./navbar.js";
import VueLodash from "vue-lodash";
import lodash from "lodash";

Vue.use(VueLodash, { lodash: lodash });

export default {
  store: store,
  name: "Film",
  components: {
    Carousel,
    Slide
  },
  data: function() {
    return {
      affiche_movie: [],
      movies: [],
      title_movie:[],
    };
  },
  computed: {
    search: {
      get: function() {
        return store.state.search;
      },
      set: function(value) {
        this.sharedState = value;
      }
    }
  },
  watch: {
    search: function(){
      this.deboucedSearch()
    }
  },
  created: function() {
    fetch(
      "https://api.themoviedb.org/3/movie/now_playing?api_key=b94d0075dc634f6f48d56ae29e58f3ce&language=en-US&page=1"
    )
      .then(response => {
        return response.json().then(data => {
          console.log(data)
          for (const element of data.results) {
              this.affiche_movie.push( `https://image.tmdb.org/t/p/w200${element.poster_path}`);
              this.title_movie.push(element.title)
          }
        });
      })
      .catch(function(error) {
        console.log("Une erreur s'est produite");
        console.log(error);
      });

    this.deboucedSearch = lodash.debounce(this.getMovies, 500);
  },
  methods: {
    getMovies() {
      this.movies.length = 0;
      fetch(
        `https://api.themoviedb.org/3/search/movie?api_key=b94d0075dc634f6f48d56ae29e58f3ce&language=en-US&query=${this.search}&page=1&include_adult=false`
      )
        .then(response => {
          return response.json().then(data => {
            console.log("Données Jsonnizées");
            console.log(data.results);
            if(this.search == "" || data.results.length == 0){
       let img_container = document.querySelector('#search_results')
       let slider = document.querySelector('#slider_news')
        img_container.style.display = "none"
        slider.style.marginLeft = "80%"
      }else{
        let img_container = document.querySelector('#search_results')
        let slider = document.querySelector('#slider_news')
        img_container.style.display = "-webkit-box"
        slider.style.marginLeft = "0"
      }
            for (const element of data.results) {
              this.movies.push(
                `https://image.tmdb.org/t/p/w200${element.poster_path}`,
                element.title,
                element.id
              );
            }
          });
        })
        .catch(function(error) {
          console.log("Une erreur s'est produite");
          console.log(error);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#new_film {
  display: flex;
  justify-content: space-around;
}
#slider_news {
  width: 20%;
}
#search_results {
  display: -webkit-box;
  width: 80%;
}
#search_results > img{
  padding: 10px;
}
</style>