import Vuex from 'vuex'

let store = new Vuex.Store({
    state: {
        search: "",
    },
    mutations: {
        setSearchValue(state, value) {
            state.search = value;
        }
    },
    actions: {
        setSearchValue(context, value) {
            context.commit('setSearchValue', value)
        },
    },
    getters: {},

})

global.store = store

export default store